<template>
    <div class="container">
        <NaviationBar />

        <div class="content">
            <div class="head">
                <div class="isLogin"  v-show="!isLogin"> 
                        <Icon type="md-person" class="un_pic"/>
                        <P>还未登录？请
                        <Button type="warning" @click="modal2 = true">点击登录</Button>
                        </P>
                 </div>

                 <div class="isLogin" v-show="isLogin"  >
                    <div class="demo-avatar"  >
                      <Avatar class="avatar"   src="https://i.loli.net/2017/08/21/599a521472424.jpg"   />
                    </div>  
                     <!-- <span>{{usernameShow}}</span> -->
                     <!-- <span>已登录</span> -->
                 </div>
          </div>
             
                    
 
    <Collapse v-model="value2" accordion class="mine_menu">
        <Panel name="1">
           设置个人资料
            <div slot="content"> 
                <i-Input class="mine_next_input"  >
                    <span class="mine_next" slot="prepend">昵 称</span>
                </i-Input>
                <i-Input  class="mine_next_input"  >
                    <span class="mine_next" slot="prepend">E-mail</span>
                </i-Input>
                <i-Input class="mine_next_input"  >
                    <span class="mine_next" slot="prepend">性 别</span>
                </i-Input>
                <i-Input class="mine_next_input"  >
                    <span class="mine_next" slot="prepend">生 日</span>
                </i-Input>
                <i-Input class="mine_next_input"  >
                    <span  class="mine_next" slot="prepend">身份证号</span>
                </i-Input>
                <i-Input class="mine_next_input"  >
                    <span class="mine_next" slot="prepend">个人描述</span>
                </i-Input>
                 <Button type="success"   long   style="height:50px;font-size:16px;margin-top:10px;margin-bottom:10px;">确认修改</Button>
            </div>
        </Panel>
        <Panel name="2">
            收货地址管理
            <div slot="content"> 
                <i-Input class="mine_next_input" v-model="companyName">
                    <span class="mine_next" slot="prepend">分公司名称</span>
                </i-Input>
                <i-Input  class="mine_next_input" v-model="userId">
                    <span class="mine_next" slot="prepend" >员工Id</span>
                </i-Input>
                <!-- <i-Input class="mine_next_input">
                    <span class="mine_next" slot="prepend">邮政编码</span>
                </i-Input>
                <i-Input class="mine_next_input">
                    <span class="mine_next" slot="prepend">生 日</span>
                </i-Input> -->
                <i-Input class="mine_next_input" v-model="useradress">
                    <span  class="mine_next" slot="prepend">详细地址</span>
                </i-Input>
               
                 <Button type="success"   long @click="addressSubmit"  style="height:50px;font-size:16px;margin-top:10px;margin-bottom:10px;">确认修改</Button>
            </div>
        </Panel>      
    </Collapse>
 
<Collapse v-model="value2" accordion class="mine_menu">
        <Panel name="3">
           我的订单
            <p slot="content">请完善信息。</p>
        </Panel>
        <Panel name="4">
            我的评价
            <p slot="content">请完善信息</p>
        </Panel>      
    </Collapse>
        
<Collapse v-model="value2" accordion class="mine_menu">
        <Panel name="5">
          商品收藏
            <p slot="content">请完善信息</p>
        </Panel>
        <Panel name="6">
           店铺收藏
            <p slot="content">请完善信息。</p>
        </Panel>
        <Panel name="7">
           浏览记录
            <p slot="content">请完善信息</p>
        </Panel>
    </Collapse>

         <Button type="error"     v-show="isLogin"  :loading="modal_loading" long @click="clearlocal" style="height:50px;font-size:16px;margin-top:20px;margin-bottom:40px;background:#FF0036;">退出登录</Button>
       
       
        <Modal v-model="modal2" width="360">
        <p slot="header" style="color:#f60;text-align:center">
            <Icon type="ios-information-circle"></Icon>
            <span>登录/注册</span>
        </p>
        <div style="text-align:center">
              
            <Tabs value="name1">
                    <TabPane label="登录" name="name1">
                         <Form ref="formInline.login" :model="formInline.login" :rules="ruleInline" >
                            <FormItem prop="user_login">
                                <i-Input type="text" class="login_item" v-model="formInline.login.user_login" placeholder="Username">
                                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                                </i-Input>
                            </FormItem>
                            <FormItem prop="password_login">
                                <i-Input type="password" class="login_item"  v-model="formInline.login.password_login" placeholder="Password">
                                    <Icon type="ios-lock-outline" slot="prepend"></Icon>
                                </i-Input>
                            </FormItem>
                            <FormItem>
                                <!-- <Button type="primary" @click="login">Login</Button> -->
                                 <Button type="success" size="large" long :loading="modal_loading" @click="login">登录</Button>
                            </FormItem>
                        </Form>
                        
                        
                    </TabPane>
                    <TabPane label="注册" name="name2"> 
                         <Form ref="formInline.sign" :model="formInline.sign" :rules="ruleInline" >
                            <FormItem prop="user_sign">
                                <i-Input type="text"  class="login_item" v-model="formInline.sign.user_sign" placeholder="Username">
                                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                                </i-Input>
                            </FormItem>
                            <FormItem prop="password_sign">
                                <i-Input type="password"    v-model="formInline.sign.password_sign" placeholder="Password">
                                    <Icon type="ios-lock-outline"    slot="prepend"></Icon>
                                </i-Input>
                            </FormItem>
                            <FormItem>
                                <!-- <Button type="primary" @click="register">sign</Button> -->
                                 <Button type="success" size="large" long :loading="modal_loading" @click="register">注册</Button>
                            </FormItem>
                        </Form>
                    </TabPane>
                   
                </Tabs>

        </div>
        <div slot="footer">
           
        </div>
    </Modal>
       
       
       
       
       
        </div>
        


        <TabBar/>
    </div>

</template>
<script>
    import TabBar from '../components/tabbar'
    import NaviationBar from '../components/naviationBar'
export default {
    name:'Min',
    data() {
        return {
            isLogin:false,
            modal2: false,
            a:false,
            usernameShow:"",
            modal_loading: false,
            value2:"",
            companyName:"",
            userId:"",
            useradress:"",
             formInline: {
                 login:{
                    user_login: '',
                    password_login: '',
                    },
                    sign:{
                    user_sign: '',
                    password_sign: '',}
                    
                },
                ruleInline: {
                    user_login: [
                        { required: true, message: 'Please fill in the user name', trigger: 'blur' }
                    ],
                    password_login: [
                        { required: true, message: 'Please fill in the password.', trigger: 'blur' },
                        { type: 'string', min: 6, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
                    ],
                    user_sign: [
                        { required: true, message: 'Please fill in the user name', trigger: 'blur' }
                    ],
                    password_sign: [
                        { required: true, message: 'Please fill in the password.', trigger: 'blur' },
                        { type: 'string', min: 6, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
                    ]
                }
            
        }
    },
      components:{
        TabBar,
        NaviationBar
    },
    created(){

         if(sessionStorage.loginName){
             this.isLogin= true;
         this.usernameShow = JSON.parse(sessionStorage.getItem("loginName"));
        }
    },
    methods:{
        addressSubmit(){
          if(this.userId||this.useradress|| this.companyName){
               if(sessionStorage.loginName){
             
              var data3= JSON.parse(sessionStorage.getItem("loginName"));
               data3[2] = this.useradress;
                data3[3] = this.companyName;
                 data3[4] = this.userId;
               var jsonObj2 = JSON.stringify(data3);
              // 更新本地数据
               sessionStorage.loginName = jsonObj2;
           
         }else{
              this.$Message.error('请登录后填写');
         }
            
          }else{
                   this.$Message.error('信息不能为空');
          }
        },
        clearlocal(){
             this.modal_loading = true;
             setTimeout(() => {
                  this.modal_loading = false;
                  this.isLogin= false;
                 sessionStorage.removeItem('loginName');
                            this.$Message.success('退出登录成功');
                        }, 2000);
        },
        goLogin(){
              this.$router.push({
                name:'login',
               
        })
        },
          tips(){
                this.modal_loading = true;
                setTimeout(() => {
                    this.modal_loading = false;
                    this.modal2 = false;
                    this.$Message.success('登录成功');
                }, 2000);
            },
              
        login(){
            var  alertMsg = "";
            // location.hash = "login";
            // 注册BMOB服务s
             var userLogin= this.formInline.login.user_login;
            var passwordLogin = this.formInline.login.password_login;
            Bmob.initialize("9e82bd8b89098646d87f57a0feb7f64c", "ad98aeb898044faad482f8ebf4c90453");
         
           var user =  Bmob.User.logIn(userLogin, passwordLogin, {
                success:(res)=>{
                    var arr =[];
                    arr.push(res._serverData.username);
                    arr.push(res._serverData.core);
                    arr.push(res._serverData.address);
                    arr.push(res._serverData.company);
                    arr.push(res._serverData.id);
                    arr.push(this.passwordLogin);
                     var jsonObj = JSON.stringify(arr);
                       // 更新本地数据
                      sessionStorage.loginName = jsonObj;
                    //   console.log(jsonObj);
                    // alert("登录成功");
                       console.log("登录成功");
                       this.usernameShow = userLogin;
                      console.log(res._serverData);
                     this.modal_loading = true;
                        setTimeout(() => {
                            this.modal_loading = false;
                            this.modal2 = false;
                             this.isLogin= true;
                            this.$Message.success('登录成功');
                        }, 2000);

                },
                error: function(user, error) {
                    alert("Error: " + error.code + " " + error.message);
                    //   this.$Message.success('登录失败');
                    }
  
                 });
 
                 
            
            
          
        },

        register(){
            var  alertMsg = "";
            // console.log(this.formInline.sign.user_sign, this.formInline.sign.password_sign);
            location.hash = "login";
            // 注册BMOB服务s
            Bmob.initialize("9e82bd8b89098646d87f57a0feb7f64c", "ad98aeb898044faad482f8ebf4c90453");
            // 注册用户
            // 创建用户
            var user = new Bmob.User();
            // 设置用户属性
            user.set("username", this.formInline.sign.user_sign);
            user.set("password", this.formInline.sign.password_sign);
            user.set("core", "2000");
            // 执行注册
            user.signUp(null, {
                success: function (user) {
                    alert("注册成功");
                //       setTimeout(() => {
                //     this.$Message.success('222222');
                // }, 2000);
                },
                error: function(user, error) {
                    alert("Error: " + error.code + " " + error.message);
                }
             });         

   }
         
    
    }
}
</script>
<style scoped>
  .container{
      width: 100%;
      background: rgb(240, 240, 240,.5);
      
  }
  .content{
      width: 90%;
      margin: 0px auto;
      position: relative;
      
  }
  .demo-avatar{
    width: 120px;
    height: 120px;
    position: absolute;
    left: 50%;
    top: 5%;
    transform: translateX(-50%);
    box-sizing:border-box;
   
}
.avatar {
    border-radius: 50%;
    width: 100%;
    height: 100%;
}
  .head{
      width: 100%;
      height: 180px;
      background : rgba(199, 198, 198, 0.5);
      border-radius: 5px;
      text-align:center;
      
  }
  .isLogin{
       width: 80%;
       height: 80%;
       margin: 0 auto;
       /* background: aqua; */
       padding-top: 40px;

  }
  .content .mine_menu{
      width: 100%;
      border-radius:5px;
      margin-top:20px;
      background: white;
      border:1px solid rgb(158, 157, 157, .5)

  }
  .mine_next_input{
      width: 90%;
      margin: 6px auto;
  }
  .mine_next{
      display: inline-block;
      width: 80px;
  }
  
</style>


