<template>
<div class="naviationBar">
    <!-- 返回按钮 -->
    <Icon class="goback-btn" @click="gobackClick" v-if="goback" type="chevron-left"></Icon>
    <h3 class="title">{{this.$store.state.title}}</h3>

</div>
</template>
<script>
export default {
    name:'NaviationBar',
    props:{
        goback:{
            type: Boolean,
            default:false
        }
    },
    methods: {
        gobackClick() {
            this.$router.go(-1);
        }
    },


}
</script>
<style scoped>
 @font-face {
        /*字体名称*/
        font-family: '宋体1122'; 
        src: url(../assets/SourceHanSerifSC-SemiBold.otf);
    }

.naviationBar{
    height: 1.4rem;
    color: aliceblue;
    background: #FF0036;
    /* background:#ed3f14; */
    position: relative;
    /* background: rgb(red, green, blue) */
}
.naviationBar .title{
    text-align: center;
    /* line-height: 3.2rem;
    letter-spacing: .5rem;
    font-size: 1rem;
    font-weight: 600; */
    letter-spacing: .05rem;
    font: normal bold 0.26rem/1.4rem '宋体1122';
}
.goback-btn{
    position: absolute;
    left: 0.16rem;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
}
</style>



