<template>
<div class="naviationBar" :style="{'background':this.$store.state.global_bgcolor}">
     <div class="head_line">
                        <div class="head_first width_body">

                          
                          <Icon type="ios-crop" size="34" style="color:white;line-height:0.7rem;" />
                          <div v-show="header_show"> 
                              <span class="market">积分商城</span>
                            <span class="welcome">欢迎你</span>
                            </div>
                            
                           <Icon type="ios-cart-outline" size="34" style="color:white;line-height:0.7rem; "/>
                        </div>
                       <div class="head_input">
                           
                            <Icon type="ios-search" size="14" style="color:gray;line-height:0.5rem;font-weight:200;"></Icon>
                            <span>搜索你要兑换的商品</span>
                        </div> 
                    </div>  
</div>
</template>
<script>
export default {
    name:'homeNav',
    data(){
        return{
          header_show:true
        }
    },
    props:{
        goback:{
            type: Boolean,
            default:false
        }
    },
    methods: {
        gobackClick() {
            this.$router.go(-1);
        }
    },


}
</script>
<style scoped>
 @font-face {
        /*字体名称*/
        font-family: '宋体1122'; 
        src: url(../assets/SourceHanSerifSC-SemiBold.otf);
    }
.width_body{
    width: 95%;
}
.naviationBar{
    /* background: red; */
     /* background: palevioletred; */
     width: 100%;
     height: 1.4rem;
       transition: all .3s linear;
}

.head_line{
  margin: 0 auto; 
  text-align: center;
  
}
.head_first{
     display: flex;
     justify-content: space-between;
     margin: 0 auto;
     /* width: 100%; */
     height: 0.7rem;
     line-height: 0.7rem;
    
       color: white;
}
.market{
    /* font-size: 0.69rem;
    font-weight: 500; */
    color: white;
     font: normal 500 0.39rem/0.7rem '宋体1122';
}
.head_input{
    
    height: 0.5rem;
     line-height: 0.5rem;
     background: rgb(230, 227, 227,.4);
       color: gray;
    font-size: 0.08rem;
    text-align: left;
    margin: 0.05rem auto;
    width: 90%;
     
      padding-left:0.05rem; 
      border-radius: 0.15rem;
      vertical-align: middle;


}
 
</style>



