<template>
    <div class="tabBar">
        <!-- 游标 -->
        <div class="cursor" :style="{'left':this.$store.state.left}">

        </div>
        <ul class="tabBar-list">
            <li class="item" v-for="(link, index) in links" :key="index">
                <Icon class="icon" :type="types[index]" size="25" color="white"></Icon>

                <router-link :to="link.href">
                    {{link.title}}
                </router-link>
            </li>
        </ul>

    </div>
</template>
<script>
export default {
    name:'TabBar',
    data() {
        return {
            types:[
                'ios-home',
                'navicon-round',
                'ios-cart',
                'ios-person'

            ],
            links:[
                {title:'主页', href:'/'},
                {title:'分类', href:'/search'},
                {title:'购物车', href:'/shoppingcar'},
                {title:'我的', href:'/min'},
            ]
        }
    },


}
</script>

<style scoped>
.tabBar{
    position: relative;
    background: #000;
    height: 0.89rem;
    overflow: hidden;
}
.tabBar .cursor{
    width: 25%;
   height: 0.89rem;
    position: absolute;
    transition: left .25s linear;
    top: 0;
    background-color:#FF0036;
}
.tabBar-list .item{
    width: 25%;
    height: 0.89;
    background: transparent;
    color: #fff;
    cursor: pointer;
    float: left;
    position: relative;
    padding: 3px 0;
    /* background: red; */
    text-align: center;
}
.tabBar-list .item a{
    display: block;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    color: #fff;
    font-size: 11px;
    text-align: center;
    /* text-align: center; */
    /* display: flex;
    justify-content: center;
    align-items: center; */
    position: relative;
    z-index: 10;
    /* background: red; */
    padding-top:30px; 
    
}
.tabBar-list .item .icon{
    /* display: block; */
    position: absolute;
    left: 50%;
    top:30%;
    transform: translateY(-50%)translateX(-50%) ;
}
</style>





