import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home'
import Details from '@/pages/details'
import Pay from '@/pages/pay'
import Login from '@/pages/login'
import Shoppingcar from '@/pages/shoppingcar'
import Min from '@/pages/min'
import Search from '@/pages/search'
import store from '../store'


Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      beforeEnter:(to,from,next)=>{
        store.commit('changeLeft', '0');
        store.commit('changeTitle', '主页');
        
        next()
      }
    },
    {
      path: '/shoppingcar',
      name: 'shoppingcar',
      component: Shoppingcar,
      beforeEnter:(to,from,next)=>{
        store.commit('changeTitle', '购物车');
        store.commit('changeLeft', '50%')
        next()
      }
    },
    {
      path: '/min',
      name: 'min',
      component: Min,
      beforeEnter:(to,from,next)=>{
        store.commit('changeTitle', '我的');
        
        next()
        store.commit('changeLeft', '75%')
      }
    },
    {
      path: '/search',
      name: 'search',
      component: Search,
      beforeEnter:(to,from,next)=>{
        store.commit('changeTitle', '搜索');
        store.commit('changeLeft', '25%')
        next()
      }
    },
    {
      path: '/details/:msg',
      name:'details',
      component: Details,
      beforeEnter:(to,from,next)=>{
        store.commit('changeTitle', '详情页');
        next()
      }
    },
    {
      path: '/pay',
      name:'pay',
      component: Pay,
      beforeEnter:(to,from,next)=>{
        store.commit('changeTitle', '支付页');
        next()
      }
    },
    {
      path: '/login',
      name:'login',
      component: Login,
      beforeEnter:(to,from,next)=>{
        store.commit('changeTitle', '登录页');
        next()
      }
    },
  ]
});
