<template>
    <div class="container">
        <NaviationBar />

        <div class="content">
            <div class="search_box">
                 <Icon type="search" class="ss" size="20"></Icon>
                 <input type="text" v-model="keywords">
            </div>
            <ul class="search_box_menu">
                          <li class="search_list  " v-for="(list, index) in filterArr()" :key="index" :data="list.id"   @click="GoDetails(list)">
                                
                                <img :src="list.img" alt="">
                                    <div class="search_right  ">
                                    <p>{{list.name}}</p>
                                    <p>{{list.price}}</p>
                                   
                                    </div>
                                     <Icon type="chevron-right" style="color:rgb(158, 157, 157)"></Icon>
                                <!-- <div :style="{display:none}" ></div> -->
                          </li>
                    </ul>
        </div>
        <TabBar/>
    </div>
</template>
<script>
    import TabBar from '../components/tabbar'
    import NaviationBar from '../components/naviationBar'
export default {
    name:'Search',
    data() {
        return {
             urls: {
               text  : '907-1',
             },
             typeLists:[],
             keywords:""
        }
    },
    components:{
        TabBar,
        NaviationBar
    },
     created(){
          let url2 ='../static/json/data.json';
        this.$axios.get(url2,{params:{
        //   showapi_appid: '66398',
        //   showapi_sign: '2f83df454e5b431abe55b5252a5aef47',
        //   cid:"9222",
        //   page:"1",
        //   keyword:"背包"
          }
        }).then(res => { // 请求成功
        // this.typeLists = res["data"]["showapi_res_body"]["pageBean"]["contentlist"];
        // res["data"]["daily"].forEach(element => {
        //       return element;
        //   })
        //  this.typeLists=res["data"].forEach((item,key) => {
        //      return data;
        //     //  val.forEach((Element)=>{
        //     //      return Element;
        //     //  });
        //  });
        let arrary = []
          arrary.push(res["data"]["daily"]);
           arrary.push(res["data"]["shampoo"]);
            arrary.push(res["data"]["sports"]);
             arrary.push(res["data"]["outdoor"]);
           
             console.log(arrary[0]);
      
              arrary.forEach((item,index) => {
                //  console.log(item);
                 item.forEach((value)=>{
                    //  console.log(value);
                       this.typeLists.push(value);
                 });
               
             });
            //  this.typeLists.push(arrary[0]);
            //  this.typeLists.push(arrary[2]);
            //  this.typeLists.push(arrary[1]);
            //  this.typeLists.push(arrary[3]);
        
        }
        );
        //  let url2 ='http://route.showapi.com/907-2';
        // this.$axios.get(url2,{params:{
        //   showapi_appid: '66398',
        //   showapi_sign: '2f83df454e5b431abe55b5252a5aef47',
        //   cid:"9222",
        //   page:"1",
        //   keyword:"背包"
        //   }
        // }).then(res => { // 请求成功
        // this.typeLists = res["data"]["showapi_res_body"]["pageBean"]["contentlist"];
        // // //["typeList"]
        // //res["data"]["showapi_res_body"]["pageBean"]["contentlist"]
        // // console.log(this.typeLists);
        // }
        // );

   },
    methods: {
        GoDetails(goods) {
            //进入详情页
            // this.val = e.target.dataset
            this.$router.push({
                name:'details',
                params:{msg:goods}

            })

         },
          filterArr(){
            let _this = this;
            return this.typeLists.filter((toy) => {
                let reg = new RegExp(_this.keywords);
                return reg.test(JSON.stringify(toy));
            })
        }
        },
}
</script>
<style scoped>
.container{
     background: rgb(240, 240, 240,.5);
}
   .content{
       width: 90%;
       margin: 0 auto;
   }
   .search_box{
       margin-top:10px;
       width: 100%;
       height: 40px;
       border-radius:10px;
       border:1px solid rgb(158, 157, 157);
       line-height: 40px
   }
   .ss{
       margin-left: 10px;
   }
   .search_box input{
      width: 300px;
      height:30px;
      border:1px solid transparent;
      outline:none;
   }
   /* .search_box input:focous{
      
     border:1px solid transparent ;
     outline:none;
   } */

   .search_box_menu{
       margin-top:0.1rem;
       width: 100%;
   }
   .search_box_menu li{
       width: 100%;
       height: 125px;
        margin-top:0.2rem;
       border-bottom: 2px solid  rgb(240, 240, 240);
       background: white;
       /* box-shadow: 0.01rem 0.01rem 0.42rem 0.12rem gray; */
   }
   .search_list img{
       width: 40%;
       height: 120px;
       vertical-align: middle;
       line-height: 125px;
   }
   .search_list .search_right{
       display: inline-block;
       width: 50%;
       height: 120px;
    vertical-align: middle;
   } 
   .search_right  p:nth-child(1){
        white-space: wrap;
        text-align: left;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        text-decoration: none;
        font-family: 微软雅黑;
        color: rgb(51, 102, 204);
   }

   .search_right  p:nth-child(2){
        white-space: nowrap;
        text-align: left;
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        text-decoration: none;
        font-family: 微软雅黑;
        color: rgb(204, 102, 51);
   }

</style>


