<template>
    <div class="container">
         <NaviationBar :goback="true"/>
          <div class="content">
            <Tabs value="name1">
                    <TabPane label="登录" name="name1">
                         <Form ref="formInline.login" :model="formInline.login" :rules="ruleInline" >
                            <FormItem prop="user_login">
                                <i-Input type="text" class="login_item" v-model="formInline.login.user_login" placeholder="Username">
                                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                                </i-Input>
                            </FormItem>
                            <FormItem prop="password_login">
                                <i-Input type="password" class="login_item"  v-model="formInline.login.password_login" placeholder="Password">
                                    <Icon type="ios-lock-outline" slot="prepend"></Icon>
                                </i-Input>
                            </FormItem>
                            <FormItem>
                                <Button type="primary" @click="login">Login</Button>
                            </FormItem>
                        </Form>
                        
                        
                    </TabPane>
                    <TabPane label="注册" name="name2"> 
                         <Form ref="formInline.sign" :model="formInline.sign" :rules="ruleInline" >
                            <FormItem prop="user_sign">
                                <i-Input type="text"  class="login_item" v-model="formInline.sign.user_sign" placeholder="Username">
                                    <Icon type="ios-person-outline" slot="prepend"></Icon>
                                </i-Input>
                            </FormItem>
                            <FormItem prop="password_sign">
                                <i-Input type="password"    v-model="formInline.sign.password_sign" placeholder="Password">
                                    <Icon type="ios-lock-outline"    slot="prepend"></Icon>
                                </i-Input>
                            </FormItem>
                            <FormItem>
                                <Button type="primary" @click="register">sign</Button>
                            </FormItem>
                        </Form>
                    </TabPane>
                   
                </Tabs>

          </div>
          <TabBar/>
    </div>
</template>

<script>
    import TabBar from '../components/tabbar'
    import NaviationBar from '../components/naviationBar'
export default {
    name:'Login',
    data() {
        return {
             formInline: {
                     login:{
                    user_login: '',
                    password_login: '',
                    },
                    sign:{
                    user_sign: '',
                    password_sign: '',}
                    
                },
                ruleInline: {
                    user_login: [
                        { required: true, message: 'Please fill in the user name', trigger: 'blur' }
                    ],
                    password_login: [
                        { required: true, message: 'Please fill in the password.', trigger: 'blur' },
                        { type: 'string', min: 6, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
                    ],
                    user_sign: [
                        { required: true, message: 'Please fill in the user name', trigger: 'blur' }
                    ],
                    password_sign: [
                        { required: true, message: 'Please fill in the password.', trigger: 'blur' },
                        { type: 'string', min: 6, message: 'The password length cannot be less than 6 bits', trigger: 'blur' }
                    ]
                }
            } 
     
    },
      components:{
        TabBar,
        NaviationBar
    },
    methods:{
         
        login() {
            var  alertMsg = "";
            // location.hash = "login";
            // 注册BMOB服务s
            var userLogin= this.formInline.login.user_login;
            var passwordLogin = this.formInline.login.password_login;
            Bmob.initialize("9e82bd8b89098646d87f57a0feb7f64c", "ad98aeb898044faad482f8ebf4c90453");
         
           var user =  Bmob.User.logIn(userLogin, passwordLogin, {
                success: function(user) {
                     var jsonObj = JSON.stringify(userLogin);
                       // 更新本地数据
                      sessionStorage.loginName = jsonObj;
                      console.log(jsonObj);
                    // alert("登录成功");
                       console.log("登录成功");
                     
                },
                error: function(user, error) {
                    alert("Error: " + error.code + " " + error.message);
                    }
  
                 });


        },

        register(){
            var  alertMsg = "";
            console.log(this.formInline.sign.user_sign, this.formInline.sign.password_sign);
            location.hash = "login";
            // 注册BMOB服务s
            Bmob.initialize("9e82bd8b89098646d87f57a0feb7f64c", "ad98aeb898044faad482f8ebf4c90453");
            // 注册用户
            // 创建用户
            var user = new Bmob.User();
            // 设置用户属性
            user.set("username", this.formInline.sign.user_sign);
            user.set("password", this.formInline.sign.password_sign);
            // 执行注册
            user.signUp(null, {
                success: function (user) {
                    alert("注册成功");
                },
                error: function(user, error) {
                    alert("Error: " + error.code + " " + error.message);
                }
             });
        
            

   }
         
    }
}
</script>

<style scoped>
  
</style>



