<template>
    <div class="container">
        <NaviationBar :goback="true" />
         <div class="content">
            <h3>商品信息确认</h3>
            <label class="content_label"><label>我的余额</label>{{score}}</label>
            <ul class="first_box">
                <li> 
                     <h4>价格信息</h4>
                     <label class="box_name">商品总价:</label>
                     <label >￥{{val}}</label>
                     <label class="box_name">运费:</label>
                     <label>￥{{move}}</label>
                     <h4>订单总计:</h4>
                     <span class="pay_total">￥{{val + move}}</span>
                </li>
                  <li> 
                     <h4>收货信息</h4>
                     <label class="box_name">收货地址:</label>
                     <label>{{address}}</label>
                     <label class="box_name">分公司:</label>
                     <label>{{company}}</label>
                     <label class="box_name">职工ID:</label>
                     <label>{{id}}</label>
                     <!-- <label class="box_name">电话:</label>
                     <label>15086622222</label> -->
                     
                </li>
                 <li> <Button type="success" class="payBtn" long  @click="refer"  style="height:50px;font-size:16px;">提交订单</Button></li>
                <li>
                        <Collapse v-model="value2" accordion class="mine_menu">
                            <Panel name="2">
                                修改地址
                        <div slot="content"> 
                            <i-Input class="mine_next_input"  >
                                <span class="mine_next" slot="prepend">收货地址</span>
                            </i-Input>
                            <i-Input  class="mine_next_input" >
                                <span class="mine_next" slot="prepend">分公司</span>
                            </i-Input>
                            <i-Input class="mine_next_input"  >
                                <span class="mine_next" slot="prepend">职工ID</span>
                            </i-Input>
                            <!-- <i-Input class="mine_next_input"  >
                                <span class="mine_next" slot="prepend">生 日</span>
                            </i-Input>
                            <i-Input class="mine_next_input"  >
                                <span  class="mine_next" slot="prepend">详细地址</span>
                            </i-Input> -->
                            <Button type="success" class=" " long   style="height:50px;font-size:16px;margin-top:10px;margin-bottom:10px;">确认修改</Button>
                           </div>     
                            </Panel>      
                        </Collapse>
                </li>
                <li>
                        <Collapse v-model="value3" accordion class="mine_menu">
                            <Panel name="2">
                                新增地址
                        <div slot="content"> 
                            <i-Input class="mine_next_input"  >
                                <span class="mine_next" slot="prepend">收货地址</span>
                            </i-Input>
                            <i-Input  class="mine_next_input" >
                                <span class="mine_next" slot="prepend">分公司</span>
                            </i-Input>
                            <i-Input class="mine_next_input"  >
                                <span class="mine_next" slot="prepend">职工ID</span>
                            </i-Input>
                            <!-- <i-Input class="mine_next_input" >
                                <span class="mine_next" slot="prepend">姓名</span>
                            </i-Input>
                            <i-Input  class="mine_next_input"  >
                                <span class="mine_next" slot="prepend">联系电话</span>
                            </i-Input>
                            <i-Input class="mine_next_input"  >
                                <span class="mine_next" slot="prepend">邮政编码</span>
                            </i-Input>
                            <i-Input class="mine_next_input" >
                                <span class="mine_next" slot="prepend">生 日</span>
                            </i-Input>
                            <i-Input class="mine_next_input"  >
                                <span  class="mine_next" slot="prepend">详细地址</span>
                            </i-Input> -->
                            <Button type="success" class=" " long   style="height:50px;font-size:16px;margin-top:10px;margin-bottom:10px;">确认修改</Button>
                           </div>     
                            </Panel>      
                        </Collapse>
                </li>
               
            </ul>
            <h3>选择支付方式</h3>
            <ul class="second_box">
                 <li>
                   <span>微信支付</span>
                    <Icon class="pay_right" type="chevron-right" style="color:rgb(158, 157, 157)"></Icon>
                 </li>
                  <li>
                   <span>支付宝</span>
                   <Icon class="pay_right" type="chevron-right" style="color:rgb(158, 157, 157)"></Icon>
                 </li>
                  <li>
                   <span>银行卡</span>
                    <Icon class="pay_right" type="chevron-right" style="color:rgb(158, 157, 157)"></Icon>
                 </li>
                  <li>
                   <span>信用卡</span>
                    <Icon class="pay_right" type="chevron-right" style="color:rgb(158, 157, 157)"></Icon>
                 </li>
                  <li>
                   <span>VISA</span>
                    <Icon class="pay_right" type="chevron-right" style="color:rgb(158, 157, 157)"></Icon>
                 </li>
                  <li>
                   <span>微信支付</span>
                    <Icon  class="pay_right" type="chevron-right" style="color:rgb(158, 157, 157)"></Icon>
                 </li>
            </ul>
         </div>
    </div>
</template>

<script>
  import NaviationBar from '../components/naviationBar'
    import '../../static/css/common.css'
export default {
    name:'Pay',
    data(){
        return{
           val:this.$route.params.msg,
           move:0,
           value2:'',
           value3:'',
           address:"重庆市南岸区",
           id:0,
           company:"南岸区分公司",
           username:'tuzi',
           score:0

        }
    },
    created(){
            if(sessionStorage.loginName){
             this.isLogin= true;
            let data= JSON.parse(sessionStorage.getItem("loginName"));
            this.username= data[0];
            this.score= data[1];
            this.address= data[2];
            this.company= data[3];
            this.id= data[3];
        }
    },
    components:{
        NaviationBar
    },
    methods:{
        refer(){
           if(this.score>this.val){
          let data2= JSON.parse(sessionStorage.getItem("loginName"));
          data2[1] = data2[1]- this.val;
          this.score = data2[1] ;
          this.val=0;
           var jsonObj2 = JSON.stringify(data2);
            // 更新本地数据
            sessionStorage.loginName = jsonObj2;
              var data3=[],
                  data5=[],
                  data6=[];
         if(sessionStorage.myList){
             
              data3= JSON.parse(sessionStorage.getItem("myGoods"));
              data5 = JSON.parse(sessionStorage.getItem("myList"));
              data6= data3.concat(data5);
           
         }else{
             data6= JSON.parse(sessionStorage.getItem("myGoods"));
         }
      
         
           var jsonObj3 = JSON.stringify(data6);
            // 更新本地数据
            sessionStorage.myList = jsonObj3;

            let data4 = [];
           var jsonObj4 = JSON.stringify(data4);
            // 更新本地数据
            sessionStorage.myGoods = jsonObj4;
            this.$Message.success('订单已提交成功');
            }else{

                 this.$Message.error('积分余额不足');
            }

        }
    }
}
</script>

<style scoped>
  .container{
    width: 100%;
    /* background: red; */
    background: rgb(240, 240, 240,.5);
      
  }
  .content{
    width: 90%;
    margin: 0px auto;
      
  }
.content h3{
    margin-top:20px; 
    white-space: nowrap;
    text-align: left;
    font-size: 0.38rem;
    font-weight: 700;
    font-style: normal;
    text-decoration: none;
    font-family: 微软雅黑;
    color: rgb(107, 107, 107);
}
.content_label{
    text-align: left;
    color: orange;
    font-size: 0.46rem;
    font-weight: 700;
}

.mine_next_input{
    width: 90%;
    margin: 6px auto;
    }
  .mine_next{
    display: inline-block;
    width: 80px;
  }
   .first_box{
    /* border:1px solid rgb(158, 157, 157, .5); */
   }
  .first_box li{
     
    margin-top:5px;
    border-radius:5px; 
    background: white;
    position: relative;
 

  }

  .first_box li h4{
      margin-left:30px; 
           white-space: nowrap;
    text-align: left;
    font-size: 18px;
    font-weight: 700;
    font-style: normal;
    text-decoration: none;
    font-family: 微软雅黑;
    color: rgb(54, 54, 54);
  }
   .first_box li label{
    display: inline-block;
        white-space: nowrap;
    text-align: left;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    text-decoration: none;
    font-family: 微软雅黑;
    color: rgb(54, 54, 54);
  }
   .first_box li p{
       position: absolute;
       top:35px;
       right: 0;
       width: 50%;
       height: 40px;

        display: block;
        white-space: wrap;
    text-align: left;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    text-decoration: none;
    font-family: 微软雅黑;
    color: rgb(54, 54, 54);
  }
  .box_name{
      display: block;
    white-space: nowrap;
    margin: 10px 45px;
    width: 40%;
    text-align: left;
    font-size: 14px;
    font-weight: 400;
    font-style: normal;
    text-decoration: none;
    font-family: 微软雅黑;
    color: rgb(134, 134, 134);
  }
  .second_box li{
       margin-top:5px;
    border-radius:5px; 
    background: white;
    position: relative;
    width: 100%;
    height: 40px;

  }
   .second_box li span{
      line-height: 40px;
      display: inline-block;
      margin-left:10px; 

  }
  .pay_right{
      position: absolute;
      top:50%;
      right: 10px;
      transform: translateY(-50%);
  }
  .pay_total{
      position: absolute;
      bottom: 5px;
      right: 80px;
      display: inline-block;
          white-space: nowrap;
    text-align: left;
    font-size: 20px;
    font-weight: 700;
    font-style: normal;
    text-decoration: none;
    font-family: 微软雅黑;
    color: rgb(204, 102, 51);
  }
</style>



