import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    //状态属性
    state:{
        isLogin:false,
        title:'主页',
        left:'0',
        global_bgcolor:"#71c5b8"
    },
    //提交修改
    mutations:{
      changeTitle(state, title){
        state.title = title;
      },
      changeLeft(state, left){
        state.left = left;
      },
      changeGlobal_bgcolor(state, global_bgcolor){
        state.global_bgcolor = global_bgcolor;
      },
    }
})


