<template>
    <div class="container">
        <NaviationBar />

        <div class="content">
            <div class="car_none" v-show="this.myshopping.length > 0?false:true">
                      
                </div>
            <div class="car_have" v-show="this.myshopping.length > 0?true:false">
             <Badge :count='this.myshopping.length' class="car_tt">
               <Icon type="ios-cart"  size="45"/>
             </Badge>
             <!-- <div class="car_edit" v-show="show" @click="show=!show"> -->
              <Button    class="car_edit" v-show="show" @click="show=!show"  type="info">编辑</Button>



              <div class="car_delete" v-show="!show">
                 
                 <Button type="info"  @click="show=!show" class="delete_ab">取消</Button>
                 
                 <Button type="warning" class="delete_select" @click="deleteRid">删除选中</Button>
             </div>
             <ul class="car_box_menu">
                 <li class="car_list" v-for="(item,index) in myshopping" :key="index" >
                     <Checkbox v-model="result[index]"  v-show="!show"  style="margin-left:5px;" @click="result[index]=!result[index]"></Checkbox>
                     <img :src="item.img" alt="">
                     <div class="car_right  ">
                                    <p>{{item.name}}</p>
                                    <p>{{item.price}}</p>
                       <Input-number :max="100" :min="1" v-model="item.number" class="changeNum"></Input-number>            
                    </div>
                 </li>
             </ul>
              <Button type="success" class="payBtn" long  @click="goPay" v-show="show" style="height:50px;font-size:16px;">前往支付</Button>
           </div>
        
        </div>
        <TabBar/>
    </div>
</template>
<script>
    import TabBar from '../components/tabbar'
    import NaviationBar from '../components/naviationBar'
export default {
    name:'Shoppingcar',
    data() {
        return {
            myshopping:[],
            show:true,
            result:[],
            forData:[],
            
     
        }
    },
          components:{
        TabBar,
        NaviationBar,
        
    },
    created(){
        if(sessionStorage.myGoods){
               this.myshopping = JSON.parse(sessionStorage.getItem("myGoods"));
            //    console.log(this.myshopping);
         
        }
        // if(this.myshopping.length){
        //     car_have =
        // }
    },
    methods:{
        goPay(){
            //进入详情页
            // this.val = e.target.dataset
            let total = 0;
            this.myshopping.forEach((item, index)=>{
                 total += Number(item.price.substring(1))*(item.number);
                    console.log(item.number);
            }); 
         
           var jsonObj = JSON.stringify(this.myshopping);
            // 更新本地数据
            sessionStorage.myGoods = jsonObj;
            this.$router.push({
                name:'pay',
                params:{msg:total}

            })
        },
        deleteRid(){
           this.myshopping.forEach((item,index) => {
                item.del = this.result[index];
            });
            this.result = [];
            let arr =[]
            this.myshopping.forEach((item,index,object) => {
                    if(!item.del) {
                    arr.push(item);
                }
            }); 

            this.myshopping = arr;
            
             
            var jsonObj = JSON.stringify(this.myshopping);
            // 更新本地数据
            sessionStorage.myGoods = jsonObj;
        }
    },
    
}
</script>
<style scoped>
.container{
    background: rgb(240, 240, 240);
}
.content{
       width: 90%;
       margin: 0 auto;
       position: relative;
       
   }
   .car_none{
       margin-top:0.4rem;
       width: 100%;
       height: 2.5rem;
       background: red;
       background: url(../assets/no-content.png)center center no-repeat;
       background-size: contain;
   }
   .car_tt{
       position: fixed;
       bottom: 20%; 
       right: 10%;
       z-index: 12;
   }
   .car_edit {
        /* white-space: wrap;
        text-align:right;
        font-size: 14px;
        font-weight: 600;
        font-style: normal;
        text-decoration: none;
        font-family: 微软雅黑;
        /* color: rgb(75, 129, 236); */
        /* color: white;
        width: 40px;
        height: 40px;
        background: rgb(75, 129, 236); */ 
       
        /* height: 40px; */
        /* line-height: 40px; */
        /* padding-right: 8px; */
        position:absolute;
        right: 5px;
        top:15px;

   }
   .car_delete{
        height: 0.1rem;
         /* height: 40px;
         line-height: 40px;
         text-align: right; */
   }
   .car_delete .delete_ab{
         position:absolute;
          right: 1.95rem;
          top:0.15rem;
   }
  .car_delete .delete_select{
      /* display: inline-block;
       white-space: wrap;
        text-align:right;
        font-size: 14px;
       font-weight: 600;
        font-style: normal;
        text-decoration: none;
        font-family: 微软雅黑;
        color: rgb(248, 175, 18); */
         position:absolute;
        right: 0.05rem;
        top:0.15rem;
  }
 .car_box_menu{
      background: white;
       margin-top:50px;
       width: 100%;
   }
   .car_box_menu li{
       width: 100%;
       height:2.25rem;
       border-bottom: 2px solid  rgb(240, 240, 240);
       position: relative;
   }
   .changeNum{
       position: absolute;
       right: 5px;
       bottom: 5px;
   }
   .car_list img{
       width: 35%;
       height: 120px;
       vertical-align: middle;
       line-height: 125px;
   }
   .car_list .car_right{
       display: inline-block;
       width: 50%;
       height: 120px;
    vertical-align: middle;
   } 
   .car_right  p:nth-child(1){
        white-space: wrap;
        text-align: left;
        font-size: 14px;
        font-weight: 400;
        font-style: normal;
        text-decoration: none;
        font-family: 微软雅黑;
        color: rgb(51, 102, 204);
   }

   .car_right  p:nth-child(2){
        white-space: nowrap;
        text-align: left;
        font-size: 18px;
        font-weight: 400;
        font-style: normal;
        text-decoration: none;
        font-family: 微软雅黑;
        color: rgb(204, 102, 51);
   }
   .payBtn{
       margin-top:10px; 
   }
    
</style>


