<template>
    <div class="container">
        <NaviationBar :goback="true" />
        <div class="content">
            <div class="title clearFix">
             <img class="img_show fl" :src="val.img" alt="">
             <ul class="title_detail fr">
                  <li>
                      <p>
                          <span>评分：</span>
                        <template>
                            <Rate disabled :value.sync="valueDisabled"></Rate>
                        </template>
                      </p>
                  </li>
                  <!-- <li>           
                      <p> <span>名称：</span>容积:30 升/重量：1000克/尺码高 48&nbsp;厘米 长 29 厘米；宽 26&nbsp;厘米</p>
                  </li>
                  <li>       
                      <p> <span>货号：</span>面料:100.0%聚酯纤维 内衬:100.0% 聚酯纤维 过肩:100.0% 锦纶</p>
                  </li>
                  <li>
                      <p><span>价格：</span>口袋：3个外口袋(1个带卡夹)，1个内口袋2个外网口袋1个隔层。</p>
                  </li> -->
                   <li>           
                      <p> <span>商品：</span>{{val.name}}</p>
                  </li>
                  <li>       
                      <p> <span>货号：</span>{{val.id}}</p>
                  </li>
                  <li>
                      <p><span>价格：</span>{{val.price}}</p>
                  </li>
             </ul>
            </div>
             <p class="img_name">{{val.name}}</p>
 <div class="price_box ">
                 <div class="price clearFix">
                       <span>{{val.price}}</span>
                       <div class="good_id fr">货号{{val.id}}</div>
                 </div>
                 <div class="number ">
                     <Input-number :max="10" :min="1" v-model="numSE"></Input-number>
                      <!-- <button  type="button" class="reduce" @click="reduce">-</button>
                      <input type="number" v-model="number">
                      <button  type="button" class="add" @click="add">+</button>   -->
                 </div>
             </div>
             <div class="bottom">
                    <i-button type="success" @click="storage(val,numSE)" >加入购物车</i-button>
                    <!-- <i-button type="success">立即购买</i-button> -->
             </div>
            <!-- <div class="goods_detail">
            <h3> 产品特点</h3>
            <ul>
                <li> 
                    <h4>背负舒适</h4>
                    <p>泡棉背负和可调肩带。胸带和腰带。拇指扣。</p>  
                </li>
                <li> 
                    <h4>透气性</h4>
                    <p>背负系统通风设计，提高舒适度。</p>  
                </li>
                <li> 
                    <h4>多层设计</h4>
                    <p>口袋：3 个外口袋 （1个带卡夹），1 个内口袋，2个外网口袋，1个隔层。</p> 
                </li>
                <li> 
                    <h4>10年保质</h4>
                    <p>质保范围不包括使用不当造成的损坏。出示产品和购物凭据才能享受这份10年质保。产品如有缺陷，可在质保期内免费维修和换货。 商业质保不影响适用于购买所在国家地区适用的任何法定质保。。</p>     
                </li> 
            </ul>                       
            </div>    -->                    
        </div> 
    </div>

</template>
<script>
    import NaviationBar from '../components/naviationBar'
    import '../../static/css/common.css'
export default {
    name:'Details',
      data() {
        return {
            val:this.$route.params.msg,
             valueDisabled: 4.5,
             number:1,
             numSE:1
                   
        }
    },
    components:{
        NaviationBar
    },
    methods:{
         
        storage(arrSes,numSE) {
            arrSes.number = numSE;
            let rootArr = [],
                filArr = [];
            if(sessionStorage.myGoods) {
                rootArr = JSON.parse(sessionStorage.myGoods);

                    filArr = rootArr.filter(function(item, index, arr) {  
                        if(item.id == arrSes.id){
                           rootArr[index].number = rootArr[index].number + numSE; 
                           return item
                         }
                      });

                    if(filArr.length == 0){
                       rootArr.push(arrSes); 
                     }   
            }else {
                rootArr.push(arrSes);
            }
            // 将新用户放入根数组中
            // 将根数组转换为JSON 对象
            var jsonObj = JSON.stringify(rootArr);
            // 更新本地数据
            sessionStorage.myGoods = jsonObj;
             this.$Message.success('商品加入购物车成功！');
        }
    }
}
</script>
<style scoped>
.container{
    position: relative;
     background: rgb(240, 240, 240,.5);
}
.content{
    width: 95%;
    margin: 0 auto;
    text-align: center;
    
}
.title{
    width: 100%;
    height: 200px;
    margin: 10px auto;
    background: white;
 
}
.img_show{
    width: 50%;
    height: 200px;  
    
}

.title_detail{
    width: 45%;
    height: 100%;
  
}

.title_detail li{
    width: 100%;
   
    text-align: left;
}
.title_detail li p span{
    display: inline-block;
    white-space: nowrap;
    text-align: left;
    font-size: 12px;
    font-weight: 400;
    font-style: normal;
    text-decoration: none;
    font-family: 微软雅黑;
    color: rgb(134, 134, 134);
} 
.title_detail li p{
    
    display: inline-block;
    width: 98%;
    /* background: red; */
    white-space: wrap;
    /* text-align: left; */
    font-size: 12px;
    font-weight: 400;
    font-style: normal;
    text-decoration: none;
    font-family: 微软雅黑;
    color: rgb(27, 27, 27);
}
.img_name{
    display: inline-block;
    margin-top: 20px;
     padding: 10px;
    width: 100%;
    font-weight: 700;
    font-style: italic;
    text-align: left;
    border-bottom:2px solid rgb(240, 240, 240,.5);
    
}
.price{
     width: 100%;
     height: 40px;
     text-align: left;
     line-height: 40px;
       
    
}
.goods_detail{
     background: white;
}
.price span{
background: yellow;
    margin-left:20px;
    width: 100px;
    height: 40px;
    font-size: 25px;
    line-height: 40px; 
    font-weight: 800;
    margin-top:10px; 
}
.price .good_id{
    font-size: 8px;
    color: #7d7e80;

}
.number{
    width: 34%;
    height: 40px;
    /* background: red; */
    margin-top: 10px;
    font-size: 12px;
    color: gray;
    /* border:1px solid gray; */
}
 

.bottom{
    width: 80%;
    height: 30px;
    position: absolute;
    bottom: 8px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 12;
    
}





.goods_detail{
    width: 100%;
    margin: 20px auto;
    text-align: left;
}
.goods_detail h3{
    font-weight: 700;
    font-style: italic;
}
.goods_detail ul{
    margin-top: 20px; 
}
.goods_detail ul li{
    width: 100%;
    /* background: red; */
    padding: 10px;
    margin-top: 10px; 
    border-bottom: 2px solid rgb(240, 240, 240,.5);
}
.goods_detail ul h4{
    font-weight: 400;
    font-style: normal;
    color: #0082c3;
}
.goods_detail ul li p{
    font-size: 12px;
} 
</style>




